@font-face {
  font-family: OwnFonts;
  src: url("PressStart2P-Regular.492382e8.ttf");
}

*, :before, :after {
  box-sizing: border-box;
  user-select: none;
}

body {
  background: linear-gradient(#3737f2 56%, #e1e14f 44%);
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  margin: 0;
  display: flex;
}

.world {
  position: relative;
  overflow: hidden;
}

.score {
  font-family: OwnFonts;
  font-size: 3vmin;
  position: absolute;
  top: 1vmin;
  right: 1vmin;
}

.start-screen {
  width: 900px;
  font-family: OwnFonts;
  font-size: 7vmin;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hide {
  display: none;
}

.ground {
  --left: 0;
  width: 300%;
  bottom: 0;
  left: calc(var(--left) * 1%);
  position: absolute;
}

.dino {
  --bottom: 0;
  height: 30%;
  left: 1%;
  bottom: calc(var(--bottom) * 1%);
  position: absolute;
}

.cactus {
  left: calc(var(--left) * 1%);
  height: 16%;
  position: absolute;
  bottom: 0;
}
